.container {
	display: flex;
	flex-direction: column;
	position: relative;
	align-items: center;
	height: 380px;
	background-color: rgb(255, 255, 255);
	box-shadow: 0, 0;
	padding-right: 10px;
	padding-top: 30px;
	transition: background-color 0.3s ease;
	min-width: 250px;
}

.container:hover {
	background-color: rgba(56, 195, 227, 0.5);
	cursor: pointer;
}

.productImage {
	width: 150px;
	height: 100px;
	margin: 50px 0 10px 0;
	object-fit: contain;
	object-position: center;
}

.logo {
	position: absolute;
	width: 150px;
	top: -30px;
}

.addToList {
	position: absolute;
	background-color: transparent;
	border: none;
	width: 40px;
	top: 20px;
	right: 17px;
	z-index: 100;
}

.addTooltip {
	position: absolute;
	background-color: rgba(63, 62, 62, 0.9);
	color: white;
	padding: 8px;
	border-radius: 3px;
	font-size: 7px;
  font-weight: bold;
	transition: opacity 0.2s;
	white-space: nowrap;
	pointer-events: none;
  opacity: 0;
	top: 30px;
	right: -40%;
	z-index: 40;
}

.addToList:hover {
	cursor: pointer;
}

.addToList:hover .addTooltip {
	opacity: 1;
}

.product-name {
	text-align: center;
	max-width: 290px;
}

.price-container {
	display: flex;
	flex-direction: column;
	align-items: center;
}

.price-container-non {
	display: flex;
	flex-direction: column;
	align-items: center;
	position: absolute;
	bottom: 5%;
}

.strike {
	text-decoration: line-through;
	color: red;
}

.discount {
	color: green;
}

.nondiscount {
	color: red;
}

.avisvare {
	font-weight: 500;
	color: white;
	background-color: #19619e;
	border-radius: 56px;
	margin-top: 25px;
	padding: 7px 20px 7px 20px;
}
